import React, { useEffect, useState } from "react";
import Back from "./common/Back";

import Slider from "@mui/material/Slider";
import { useDispatch, useSelector } from "react-redux";
import { addToCart1, removeFromCart1 } from "../redux/CartSlice1";
import { Link, useLocation, useNavigate } from "react-router-dom";
import axios from "axios";
import { toast, ToastContainer } from "react-toastify";
import { Badge, message } from "antd";
import NotificationBanner from "./Notification";
import ScrollToTop from "./scrolltotop";
import Cookies from 'js-cookie';
import CryptoJS from 'crypto-js';

const Scheduled = () => {
  const dispatch = useDispatch();
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedToken = Cookies.get('token');
  const token = encryptedToken ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(CryptoJS.enc.Utf8) : null;
  const cart = useSelector((state) => state.cart1);
  const encryptedUser = Cookies.get('user');
  // const user = JSON.parse(CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(CryptoJS.enc.Utf8))||null;
  // const encryptedUser = Cookies.get('user');
  const user = encryptedUser ? JSON.parse(CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(CryptoJS.enc.Utf8)) : null;
  let showText = Cookies.get("AllTextHead");
  const textEncy = Cookies.get('AllText');

  const AllText = textEncy ? JSON.parse(CryptoJS.AES.decrypt(textEncy, encryptionKey).toString(CryptoJS.enc.Utf8)) : null;
  const [data, setData] = useState([]);
  const [data1, setData1] = useState([]);

  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;
  const location = useLocation();
  const { title } = location?.state || "";

  const [openCategory, setOpenCategory] = useState(null);
  const [categorydata, setcategorydata] = useState([]);
  const [subcategorydata, setsubcategorydata] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState(title);
  const [selectedsubCategory, setSelectedsubCategory] = useState("");


  const getcategory = async () => {
    let res = await axios.get("https://majesticmall.net/api/admin/getCategory", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    if ((res.status = 200)) {
      setcategorydata(
        res.data?.category?.filter(
          (item) => item?.status === "Active"
        )
      );
    }
  };

  const getsubcategory = async () => {
    let res = await axios.get(
      "https://majesticmall.net/api/admin/getSubcategory", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    }
    );
    if ((res.status = 200)) {
      setsubcategorydata(res.data?.Subcategory?.filter(
        (item) => item?.status === "Active"
      ));
    };
  };

  useEffect(() => {
    getcategory();
    getsubcategory();
  }, []);

  const categoryid = categorydata?.find((ele) => ele?.categoryName == "Fruits & Vegetables")

  const isWithinAvailableTime = () => {
    const now = new Date();
    const day = now.getDay();
    const hours = now.getHours();

    if ((day === 6 && hours >= 12) || day === 0 || (day === 1 && hours < 12)) {
      return true;
    }

    return false;
  };



  const getproducts = async () => {
    let res = await axios.get(
      "https://majesticmall.net/api/admin/Getproductlist", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    }
    );

    if (res.status === 200) {
      const activeProducts = res.data?.ProductList?.filter(
        (item) => item.status === "Active"
      );
      setData(activeProducts);
    }
  };
  const getproducts1 = async () => {
    let res = await axios.get(
      "https://majesticmall.net/api/admin/Getfruitslist", {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    }
    );

    if (res.status === 200) {
      const activeProducts = res.data?.fruitsList?.filter(
        (item) => item.status === "Active"
      );

      const available = isWithinAvailableTime();
      setIsAvailable(available);

      const filteredProducts = activeProducts?.filter((item) => {
        return available;
      });

      setData1(activeProducts);

      const initialSelectedWeight = {};
      filteredProducts.forEach((product) => {
        initialSelectedWeight[product?._id] = !user?.club
          ? product?.weight?.[0]
          : product?.Clubweight?.[0];
      });
      setSelectedWeight(initialSelectedWeight);
    }
  };

  useEffect(() => {
    getproducts();
    getproducts1();
  }, []);

  const [selectedWeight, setSelectedWeight] = useState({});
  const [isAvailable, setIsAvailable] = useState(true);
  const [itemQuantities, setItemQuantities] = useState({});

  const handleQuantityChange = (item, change) => {
    if ((item.stock - item?.sellproduct) <= 0 || (item.stock - item?.sellproduct) < change) return message.error("Out of stock");
    setItemQuantities((prev) => ({
      ...prev,
      [item._id]: Math.max((prev[item._id] || 1) + change, 1),
    }));
  };

  const [notification, setNotification] = useState({ show: false, message: '' });
  const handleAddToCart = (item) => {
    if ((item.stock - item?.sellproduct) <= 0) return message.error("Out of stock");
    if (Object.keys(item).length == 0) return message.error("Product not found");
    item.weight[0].discount = item?.discount;
    item.Clubweight[0].discount = item?.clubdiscount;

    const weightToAdd = selectedWeight[item?._id] ||
      (!user?.club ? item?.weight[0] : item?.Clubweight[0]);

    const quantity = itemQuantities[item?._id] || 1;

    // setNotification({ 
    //   show: true, 
    //   message: `Success: You have added ${item?.productname} to your cart!`
    // });
    message.success(`Success: You have added ${item?.productname} to your cart!`)

    dispatch(
      addToCart1({
        ...item,
        selectedWeight: weightToAdd,
        quantity,
      })
    );
  };

  const handleRemoveFromCart = (item) => {
    const weightToRemove = selectedWeight[item?._id] ||
      (!user?.club ? item?.weight[0] : item?.Clubweight[0]);

    dispatch(
      removeFromCart1({
        _id: item._id,
        selectedWeight: weightToRemove,
      })
    );

    setItemQuantities((prev) => ({
      ...prev,
      [item._id]: Math.max((prev[item?._id] || 0) - 1, 0),
    }));
  };

  const handleClick = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const data3 = [...data1, ...data];

  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = data3?.filter((ele) => ele?.category == categoryid?._id)?.slice(indexOfFirstItem, indexOfLastItem);
  const totalPages = Math.ceil(data3?.filter((ele) => ele?.category == categoryid?._id)?.length / itemsPerPage);
  const navigate = useNavigate();

  const handleSubscribe = (val) => {
    const weightToAdd =
      selectedWeight[val?._id] ||
      (!user?.club ? val?.weight[0] : val?.Clubweight[0]);
    if (!user) {
      toast.info("Please login as a user to subscribe.");
    } else {
      navigate(`/subscribe/${val?._id}`, {
        state: { selectedWeight: weightToAdd },

      });
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    }
  };

  const isItemInCart = (item) => {
    return cart?.items1?.some(cartItem => cartItem?._id === item?._id);
  };

  const scrollToTop = (val) => {
    navigate(`/shop-detail1/${val?._id}`);
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  return (
    <div>
      <ToastContainer />
      <ScrollToTop />
      <NotificationBanner
        message={notification.message}
        show={notification.show}
        onClose={() => setNotification({ ...notification, show: false })}
      />
      <Back title="Scheduled delivery will be delivered on Tuesday" />

      <div className="sm:mx-20 lg:flex 2xl:mx-40">
        <div
          className="grid grid-cols-1 gap-2 mt-10 md:grid-cols-3 xl:grid-cols-5 xl:gap-x-5 xl:gap-y-0"
          style={{ height: "auto" }}
        >
          {currentItems.map((val) => {
            const currentSelectedWeight = selectedWeight[val._id];
            const itemQuantity = itemQuantities[val._id] || 1;
            const inCart = isItemInCart(val);
            return (
              <Badge.Ribbon text={val.tag} color="#f7ab27">
                <div
                  key={val._id}
                  className="box border rounded-lg border-#f7ab27-400 mx-auto w-full  max-w-xs shadow-lg hover:shadow-xl transition-shadow duration-300 bg-white"
                  style={{ height: "300px", marginBottom: "5px" }}
                >
                  <div
                    className="relative overflow-clip"
                  >
                    <img
                      className="w-full transition-transform duration-500 ease-in-out rounded-lg shadow-lg hover:scale-105"
                      src={`https://majesticmall.net/product/${val?.Photos[0]?.file}`}
                      alt="Product"
                      loading="lazy"
                      style={{
                        height: "130px",
                        width: "350px",
                        marginTop: "10px",
                        objectFit: "contain",
                        boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
                        cursor: "pointer"
                      }}
                      onClick={() => scrollToTop(val)}
                      onError={(e) => {
                        e.target.src = '../img/example.jpg'; // Replace with your default image URL
                      }}
                    />
                    {/* <span
                    className="absolute px-1 py-1 text-white top-1 rounded-xl"
                    style={{ backgroundColor: "#ffb524", fontSize: "13px" }}
                  >
                    {val.tag}
                  </span> */}
                  </div>
                  <div
                    className="text-center"

                  >
                    <h3
                      className="mt-4 font-semibold text-l"
                      style={{ color: "#45595b", cursor: "pointer" }}
                      onClick={() => scrollToTop(val)}
                    >
                      {val.productname.slice(0, 20)}
                    </h3>
                    <select
                      name="option[504]"
                      id="input-option504"
                      className="form-control1 weight resp-select-box"
                      style={{
                        maxWidth: "200px",
                        fontSize: "12px",
                        border: "1px solid #88ac46",
                      }}
                      onChange={(e) => {
                        const selectedWeightData = JSON.parse(e.target.value);

                        const selectedDiscount =
                          selectedWeightData.discountType === "discount"
                            ? val.discount
                            : val.clubdiscount;
                        setSelectedWeight((prev) => ({
                          ...prev,
                          [val._id]: {
                            ...selectedWeightData,
                            discount: selectedDiscount,
                            discountType: selectedWeightData.discountType,
                          },
                        }));
                      }}
                      value={JSON.stringify(currentSelectedWeight)}
                    >
                      {!user?.club &&
                        val.weight?.map((e) => (
                          <option
                            key={`weight-${e.id}`}
                            style={{ fontSize: "12px" }}
                            value={JSON.stringify({
                              ...e,
                              discount: val.discount,
                              discountType: "discount",
                            })}
                          >
                            {e.value} {e.units} - Rs{" "}
                            {(e.price - e.price * (val.discount / 100)).toFixed(
                              2
                            )}
                          </option>
                        ))}
                      {val.Clubweight?.map((e) => (
                        <option
                          key={`clubweight-${e.id}`}
                          value={JSON.stringify({
                            ...e,
                            discount: val.clubdiscount,
                            discountType: "clubdiscount",
                          })}
                          disabled={!user || user.club === false}
                          style={{
                            color:
                              !user || user.club === false ? "#aaa" : "inherit",
                            fontSize: "12px",
                          }}
                        >
                          {e.value} {e.units} - Rs{" "}
                          {(
                            e.price -
                            e.price * (val.clubdiscount / 100)
                          ).toFixed(2)}
                          {!user?.club && " (Club Members Only)"}
                        </option>
                      ))}
                    </select>
                  </div>
                  {(val.stock - val?.sellproduct) <= 0 ? (<p style={{ color: "red", paddingTop: "27px", textAlign: "center", fontWeight: "500" }}>Out of Stock</p>) : <div className="flex items-center justify-between mt-4">
                    <div className="flex justify-center flex-grow">
                      {val.subscribe === "Subscription" ? (<>
                        {inCart ?
                          <button
                            onClick={() => {
                              !AllText?.category
                                ? val.category == categoryid?._id
                                  ? showText
                                    ? message.error(showText)
                                    : handleAddToCart(val)
                                  : handleAddToCart(val)
                                : handleAddToCart(val);
                            }}
                            className="px-3 py-1 text-xs font-semibold text-white rounded-2xl"
                            style={{ backgroundColor: "#f7ab27", color: "#fff" }}
                          >
                            Update cart
                          </button> : <button
                            onClick={() => {
                              !AllText?.category
                                ? val.category == categoryid?._id
                                  ? showText
                                    ? message.error(showText)
                                    : handleAddToCart(val)
                                  : handleAddToCart(val)
                                : handleAddToCart(val);
                            }}
                            className="px-3 py-1 text-xs font-semibold text-white rounded-2xl"
                            style={{
                              backgroundColor: !AllText?.category &&
                                val.category == categoryid?._id
                                ? showText
                                  ? "rgb(204 225 166)"
                                  : "#000"
                                : "#000", color: "#fff"
                            }}
                          >
                            Buy Once
                          </button>} </>
                      ) : (
                        <>
                          <div className="flex items-center mr-2">
                            <button
                              className="px-2 py-1 text-sm font-semibold"
                              style={{
                                backgroundColor: "#ffb524",
                                color: "#fff",
                                borderRadius: "50%",
                              }}
                              onClick={() => {
                                !AllText?.category
                                  ? val.category == categoryid?._id
                                    ? showText
                                      ? message.error(showText)
                                      : handleRemoveFromCart(val, 1)
                                    : handleRemoveFromCart(val, 1)
                                  : handleRemoveFromCart(val, 1);
                              }}
                            >
                              -
                            </button>
                            <span className="px-3">{itemQuantity}</span>
                            <button
                              className="px-2 py-1 text-sm font-semibold"
                              style={{
                                backgroundColor: "#ffb524",
                                color: "#fff",
                                borderRadius: "50%",
                              }}
                              onClick={() => {
                                !AllText?.category
                                  ? val.category == categoryid?._id
                                    ? showText
                                      ? message.error(showText)
                                      : handleQuantityChange(val, 1)
                                    : handleQuantityChange(val, 1)
                                  : handleQuantityChange(val, 1);
                              }}
                            >
                              +
                            </button>
                          </div>
                          {inCart ? <button
                            onClick={() => handleAddToCart(val)}
                            className="px-3 py-1 text-xs font-semibold text-white rounded-2xl"
                            style={{
                              backgroundColor: "#f7ab27",
                              color: "#fff",
                            }}
                          >
                            Update cart
                          </button> : <button
                            onClick={() => {
                              !AllText?.category &&
                                val.category == categoryid?._id
                                ? showText
                                  ? message.error(showText)
                                  : handleAddToCart(val)
                                : handleAddToCart(val);
                            }}
                            className="px-3 py-1 text-xs font-semibold text-white rounded-2xl"
                            style={{
                              backgroundColor: !AllText?.category &&
                                val.category == categoryid?._id
                                ? showText
                                  ? "rgb(204 225 166)"
                                  : "#000"
                                : "#000",
                              color: "#fff",
                            }}
                          >
                            Add to cart
                          </button>}
                        </>
                      )}
                    </div>
                    {val.subscribe === "Subscription" && (
                      <button
                        onClick={() => handleSubscribe(val)}
                        className="px-3 py-1 text-xs font-semibold text-white rounded-2xl"
                        style={{
                          backgroundColor: "#0c363b",
                          marginRight: "15px",
                        }}
                      >
                        Subscribe
                      </button>
                    )}
                  </div>}

                </div>
              </Badge.Ribbon>
            );
          })}
        </div>
      </div>
      <ul className="flex items-center justify-center mx-4 mt-20 next">
        <li
          className="py-2 px-4 mx-1 border border-#f7ab27-400 rounded-lg cursor-pointer"
          style={{ color: currentPage === 1 ? "#d1d5db" : "#45595b" }}
          onClick={() => handleClick(currentPage - 1)}
          disabled={currentPage === 1}
        >
          &#8656;
        </li>
        {[...Array(totalPages)].map((_, index) => (
          <li
            key={index}
            className={`py-2 px-4 mx-1 border border-#f7ab27-400 rounded-lg cursor-pointer ${currentPage === index + 1 ? "bg-[#000] text-white" : ""
              }`}
            style={{ color: "#45595b" }}
            onClick={() => handleClick(index + 1)}
          >
            {index + 1}
          </li>
        ))}
        <li
          className="py-2 px-4 mx-1 border border-#f7ab27-400 rounded-lg cursor-pointer"
          style={{ color: currentPage === totalPages ? "#d1d5db" : "#45595b" }}
          onClick={() => handleClick(currentPage + 1)}
          disabled={currentPage === totalPages}
        >
          &#8658;
        </li>
      </ul>
    </div>
  );
};

export default Scheduled;
